<template>
    <div class="edit-setting">
        <h2 class="title">
            {{$route.name == '修改配置' ? '修改配置规则' : '配置新规则'}}
        </h2>
        <Form :model="settingData" class="form-group" label-position="right" :label-width="100">
            <FormItem label="类型" prop="type">
                <RadioGroup v-model="settingData.type" type="button">
                    <Radio :label="0">文本</Radio>
                    <Radio :label="1">点击</Radio>
                    <Radio :label="2">关注</Radio>
                </RadioGroup>
            </FormItem>
             <FormItem v-if="settingData.type == 0" label="用户回复文本" prop="text">
                <Input size="large" type="text" v-model="settingData.text" placeholder="请输入用户回复文本"></Input>
            </FormItem>
            <FormItem v-else-if="settingData.type == 1" label="点击关键词" prop="key">
                <Input size="large" type="text" v-model="settingData.key" placeholder="请输入点击关键词"></Input>
            </FormItem>
            <FormItem label="类型" prop="replyType">
                <RadioGroup v-model="settingData.replyType" type="button">
                    <Radio :label="0">文本/图文</Radio>
                    <Radio :label="1">图片</Radio>
                </RadioGroup>
            </FormItem>
            <FormItem v-if="settingData.replyType == 0" label="消息模板ID" prop="msgId">
                <Input size="large" type="text" v-model="settingData.msgId" placeholder="请输入消息模板ID"></Input>
            </FormItem>
            <FormItem v-else-if="settingData.replyType == 1" label="图片" prop="showUrl">
                <Upload action="http://test.oorggt.top/reply/upload" :show-upload-list="false" name="imageFile" :on-success="handleSuccess">
                    <Button icon="ios-cloud-upload-outline">点击上传</Button>
                    <br />
                    <img v-if="imgUrl" :src="imgUrl" alt="" width="200" style="margin-top:10px;">
                </Upload>
            </FormItem>
            <FormItem>
                <Button class="edit-btn" size="large" type="primary" @click="submitForm">提交</Button>
                <Button class="edit-btn" size="large" type="error" @click="resetForm">重置</Button>
                <Button class="edit-btn" size="large" type="warning" @click="cancel">取消</Button>
            </FormItem>
        </Form>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                settingData: {
                    type: 0,
                    replyType: 0
                },
                imgUrl: '',
                url: '',
                code: null
            };
        },
        mounted() {
            this.code = this.$route.query.code
            if (this.$route.name === '修改配置') {
                this.settingData = this.$route.query.data;
                this.url = this.settingData.url
                this.imgUrl = this.settingData.showUrl
            }
        },
        methods: {
            handleSuccess(res, file) {
                this.url = "./public/uploads/" + res.filename;
                this.imgUrl = "http://test.oorggt.top/uploads/" + res.filename;
            },
            cancel() {
                this.resetForm()
                this.$router.push({name: '配置公号'})
            },
            resetForm() {
                this.settingData = {
                    type: 0,
                    replyType: 0
                }
                this.imgUrl = this.url = ''
            },
            submitForm() {
                if (this.$route.name === '配置新规则') {
                    this.$axios.post('/reply/create', {
                        code: this.code,
                        replyType: this.settingData.replyType,
                        type: this.settingData.type,
                        url: this.url,
                        showUrl: this.imgUrl,
                        text: this.settingData.text,
                        key: this.settingData.key,
                        msgId: this.settingData.msgId
                    }).then(res => {
                      if(res.data) {
                        this.$Message.info('创建成功')
                        window.location.href = '/manage/gonghao/tuoguan/setting'
                        this.resetForm()
                      }
                    });
                } else if(this.$route.name === '修改配置') {
                    this.$axios.post('/reply/update', {
                        code: this.code,
                        replyType: this.settingData.replyType,
                        id: this.settingData._id,
                        type: this.settingData.type,
                        text: this.settingData.text,
                        url: this.url,
                        showUrl: this.imgUrl,
                        key: this.settingData.key,
                        msgId: this.settingData.msgId
                    }).then(res => {
                      if(res.data) {
                        this.$Message.info('修改成功')
                        window.location.href = '/manage/gonghao/tuoguan/setting'
                        this.resetForm()
                      }
                    });
                }
            }
        }
    };
</script>

<style scoped>
    .edit-setting {
        width: 500px;
        margin-left: 200px;
    }
    .form-group {
        width: 500px;
    }
    .title {
        margin-bottom: 50px;
    }
    .edit-btn {
        margin-right: 20px;
    }
</style>
